import React, {useEffect, useState} from 'react';
import logo from './logo.svg';
import './App.css';

function App() {
  const [beck, setBack] = useState([{}]);

  useEffect(() => {
    fetch("/prg")
        .then(res => res.json())
        .then(data => setBack(data));
  }, []);
  return (
    <div className="App">

    </div>
  );
}

export default App;
